import Vue from 'vue';
import VueRouter from 'vue-router';
import VueResource from 'vue-resource';
import App from './App.vue';
import { routes } from './routes';
import { store } from './store/store';

Vue.use(VueRouter);
Vue.use(VueResource);

Vue.http.options.root = "https://my-work-tasks.firebaseio.com/";

export const eventBus = new Vue({
  methods: {
    leaveModal() {
      this.$emit('leftTheModal', option);
    }
  }
})

const router = new VueRouter({
  mode: 'history',
  routes
})

new Vue({
  el: '#app',
  router,
  store,
  render: h => h(App)
})



